  background-position: center center;
}
.timer-widget .timer-slider .timer-range::after {
  background-image: url(https://static.iviva.com/images/Udhayimages/moon.png);
  content: "";
  width: 35px;
  height: 35px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  bottom: -20px;
  right: -45px;
  background-repeat: no-repeat;
  background-position: center center;
  transform: rotate(160deg);
}
.timer-widget .timer-slider .timer-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #fefefe;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 0px 3px #9f9f9f;
}
.timer-widget .timer-slider .timer-slider {
