  margin-top: -1px;
  /* text-shadow: 1px 1px 1px #f70606, 0 0 1em #f4433659, 0 0 0.2em #ffeb3b4f; */
  font-weight: bold;
}
.smart-city_box .incident-manage-box.smart-city_box .uxp-widget-title-bar {
  width: 99%;
}
.smart-city_box .uxp-select-dropdown-container {
  background-color: #629284;
  color: #ffffff;
}
.smart-city_box .uxp-select-dropdown-container .uxp-search-box-container .search-box-container {
  background-color: #629284;
  color: #ffffff;
}
.smart-city_box .uxp-select-dropdown-container .uxp-search-box-container .search-box-container .uxp-form-input {
  color: #ffffff;
}
.smart-city_box .uxp-select-dropdown-container .uxp-select-dropdown {
  color: #ffffff;
}
.smart-city_box .uxp-select-dropdown-container .uxp-select-dropdown .data-list-container .data-list {
  background-color: #629284;
  color: #ffffff;
}
.smart-city_box .uxp-select-dropdown-container .uxp-select-dropdown .data-list-container .data-list .uxp-select-option-container.highlighted {
  background-color: rgb(2, 79, 58);
  color: #ffffff;
}
.smart-city_box .uxp-select-dropdown-container .uxp-select-dropdown .data-list-container .data-list .uxp-select-option-container.selected {
  background-color: #216e57;
}

/**************Serach box********************/
.searchBox-widget {
  display: inline-block;
  width: 100%;
}
.searchBox-widget .searchbox-container {
  position: relative;
  align-items: end;
  text-align: right;
  background: #fff;
  border: 1px solid #033649;
  border-radius: 5px;
  padding: 1px 0;
}
.searchBox-widget .searchbox-container .searchbox {
  padding: 9px 5px;
  border-radius: 5px;
  font-size: 1em;
  width: 91%;
  text-align: left;
  outline: none;
  margin-right: 6%;
  border: 1px solid transparent;
}
.searchBox-widget .searchbox-container .searchbox:focus {
  outline: none;
}
.searchBox-widget .searchbox-container .clear-button {
  position: absolute;
  top: 0.6em;
  right: 0.7em;
  font-size: 1.1em;
  font-weight: bold;
  cursor: pointer;
}
.searchBox-widget ul.search-list {
  padding: 0;
  margin: 1.5em 0 0 1em;
  color: #fff;
  height: 5em;
  overflow: auto;
  text-align: left;
  width: 90%;
}
.searchBox-widget ul.search-list li {
  list-style: none;
  margin: 0 0 3px 0;
  padding: 6px 5px;
  width: 100%;
  cursor: pointer;
}
.searchBox-widget ul.search-list li:hover {
  background: rgba(17, 8, 8, 0.708);
}

.uxp-select-dropdown-container {
  background-color: #629284;
  color: #ffffff;
}
.uxp-select-dropdown-container .search-box-container {
  background-color: #629284;
  color: #ffffff;
}
.uxp-select-dropdown-container .search-box-container:hover {
  background-color: #629284;
  color: #ffffff;
}
.uxp-select-dropdown-container .data-list-container .data-list {
  background-color: #629284;
  color: #ffffff;
}
.uxp-select-dropdown-container .data-list-container .data-list .uxp-select-option-container.highlighted {
  background-color: rgb(2, 79, 58);
  color: #ffffff;
}
.uxp-select-dropdown-container .data-list-container .data-list .uxp-select-option-container.selected {
  background-color: #216e57;
}

.uxp-select-dropdown-container .uxp-select-dropdown-footer > .uxp-button {
  display: none;
}

.averagevalue-container {
  text-align: center;
  color: white;
  font-weight: 900;
}

.uxp-select-dropdown-container.dynamic-select {
  width: 200px !important;
}

.smart-city_box.waste-bin-box.work-order-box .smart-city-content .status-content .status p::after {
  display: none;
}
.smart-city_box.waste-bin-box.work-order-box .smart-city-content .status-content .status.Attention h3 {
  color: rgb(255, 113, 8);
}
.smart-city_box.waste-bin-box.work-order-box .smart-city-content .status-content .status.acknowledgement h3 {
  color: #00ceff;
}

.uxp-search-box-icons .uxp-icon-button .icon-container .icon {
  filter: invert(1) brightness(2.5);
}
.uxp-search-box-icons .uxp-icon-button .icon-container .icon .close {
  filter: invert(1) brightness(2.5);
